.wrapper_message{
      width: fit-content;
      height: 4rem;
      position: relative;
      top: 0;
      background: var(--light);
      border-radius: .4rem;
      box-shadow: 1rem 1rem 15px rgba(158, 169, 183, .4);
      --success: #42d437;
      --error: #ff5353;
      --warning: #fbb83a;
}

.wrapper_message:before{
      width: .5rem;
      height: 4rem;
      border-radius: .4rem 0 0 .4rem;
      top: 0;
      left: 0;
}

.wrapper_message span{
      width: 4rem;
      height: 4rem;
      display: flex;
      align-items: center;
      justify-content: center;
}

.wrapper_message span *{
      font-size: 1.8rem;
}

.wrapper_message p{
      width: fit-content;
      height: 4rem;
      line-height: 4rem;
      padding-right: 2rem;
      color: var(--text);
      font-size: 1.4rem;
      white-space: nowrap;
}

.wrapper_message.success:before{
      background: var(--success);
}

.wrapper_message.success span *{
      color: var(--success);
}

.wrapper_message.warning:before{
      background: var(--warning);
}

.wrapper_message.warning span *{
      color: var(--warning);
}

.wrapper_message.error:before{
      background: var(--error);
}

.wrapper_message.error span *{
      color: var(--error);
}

.wrapper_message.success.show,
.wrapper_message.warning.show,
.wrapper_message.error.show{
      animation: show_message .3s ease-out forwards;
}

@keyframes show_message{
      0%{
            transform: translateY(0);
      }
      100%{
            transform: translateY(-15rem);
      }
}