.header_content{
      height: 8rem;
      background: var(--bg-grad-button);
      box-shadow: 0 .1rem rgba(255,255,255,.5), 0 1rem 10px rgba(158, 169, 183, .15);
}

.container.header{
      height: 100%;
      flex-direction: column;
      align-items: center;
      justify-content: center;
}

.header_content h1{
      font-size: 3.2rem;
}

.header_navigator{
      gap: 2rem;
}

.header_navigator a{
      font-size: 1.4rem;
      color: var(--text);
}

.header_navigator a:hover{
      text-decoration: underline;
}