.input_color,
.input_glow{
      width: 4rem;
      height: 4rem;
      position: relative;
}

.input_color label,
.input_glow label{
      width: 4rem;
      height: 4rem;
      background: var(--bg-grad-button);
      box-shadow: var(--shadow);
      display: flex;
      align-items: center;
      justify-content: center;
      border-radius: 50%;
      position: relative;
}

.input_color p,
.input_glow p{
      position: absolute;
      padding: .75rem 1rem;
      border-radius: .3rem;
      background: var(--tooltip-bg);
      z-index: 3;
      top: -4rem;
      left: 50%;
      transform: translateX(-50%);
      box-shadow: .5rem .5rem 6px rgba(208, 212, 217, .5);
      font-size: 1.2rem;
      transition: .2s; opacity: 0;
}

.input_color p:before,
.input_glow p:before{
      width: 1rem;
      height: 1rem;
      background: var(--tooltip-bg);
      left: calc(50% - .5rem);
      bottom: -.5rem;
      transform: rotate(45deg);
}

.input_color:hover p,
.input_glow:hover p{
      opacity: 1;
}

.input_color label span,
.input_glow label span{
      width: 2.4rem;
      height: 2.4rem;
      border-radius: 50%;
}

.input_color input,
.input_glow input{
      position: absolute;
      top: 0;
      left: 0;
      appearance: none;
      width: 4rem;
      height: 4rem;
      border-radius: 50%;
      opacity: 0;
      z-index: 2;
}

/** input check 2 */

.input_check{
      width: 8rem;
      height: 3.6rem;
      position: relative;
      border-radius: 999px;
}

.input_check__status{
      width: 100%;
      height: 100%;
      flex-direction: column;
      align-items: center;
      justify-content: space-around;
}

.input_check__status span{
      font-size: 1.4rem;
      font-weight: 600;
      text-transform: uppercase;
}

.input_check__status span.on{
      color: var(--green);
}

.input_check__status span.off{
      color: var(--red-100);
}

.check_input__item{
      appearance: none;
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      border-radius: 999px;
      cursor: pointer;
      background: #d0d4d9;
      box-shadow: var(--inner-shadow);
      transition: .2s ease;
}

.check_input__item:checked{
      background: #b0d088;
}

.check_input__item:before{
      width: 4rem;
      height: 4rem;
      border-radius: 50%;
      background: var(--bg-grad-button);
      box-shadow: var(--shadow-input);
      transition: .3s cubic-bezier(0, .53, .51, .98);
      top: 50%;
      left: -.2rem;
      border: solid .1rem rgba(255, 255, 255, .2);
      transform: translateY(-50%);
}

.check_input__item:checked:before{
      left: calc(100% - 3.8rem);
      top: 50%;
}