.app_content{
      width: 100%;
      height: 100vh;
      min-height: 108rem;
      position: relative;
}

.namegrad_content{
      height: auto;
      padding: 5rem 0;
}

.container.namegrad{
      flex-direction: column;
      align-items: center;
}

.namegrad_text{
      position: relative;
      z-index: 2;
}

.namegrad_text__effect{
      width: fit-content;
      height: fit-content;
      font-family: var(--ft-1);
      font-size: 4rem;
      color: transparent;
      background-image: repeating-linear-gradient(90deg, #68dfb5, #ee7dd9 50%);
      background-size: 200% 100%;
      background-position: left top;
      -webkit-background-clip: text;
      -moz-background-clip: text;
      background-clip: text;
}

.namegrad_text__effect.glow:before{
      content: attr(data-glow);
      color: var(--glow);
      z-index: -1;
      filter: blur(7.5px);
}

.namegrad_text__effect.wave{
      animation: wave var(--speed) linear infinite;
}

@keyframes wave{
      100%{
            background-position: right top;
      }
}

.namegrad_control{
      margin-top: 3rem;
      align-items: center;
      height: 5rem;
      gap: 3rem;
}

.namegrad_color__input{
      gap: 2rem;
      align-items: center;
}

.btn_add__input{
      width: 4rem;
      height: 4rem;
      background: var(--bg-grad-button);
      box-shadow: var(--shadow);
      border-radius: 50%;
      position: relative;
}

.btn_add__input:before,
.btn_save__preset:before{
      content: attr(data-tooltip);
      padding: .75rem 1rem;
      white-space: nowrap;
      background: var(--tooltip-bg);
      border-radius: .3rem;
      box-shadow: .5rem .5rem 6px rgba(208, 212, 217, .5);
      top: 50%;
      right: 0;
      transform: translate(120%, -50%);
      color: var(--text);
      transition: .2s;
      opacity: 0;
}

.btn_add__input:after,
.btn_save__preset:after{
      width: 1rem;
      height: 1rem;
      background: var(--tooltip-bg);
      top: calc(50% - .5rem);
      right: -1.9rem;
      transform: rotate(45deg);
      transition: .2s;
      opacity: 0;
}

.btn_add__input:hover:before,
.btn_add__input:hover:after,
.btn_save__preset:hover:before,
.btn_save__preset:hover:after{
      opacity: 1;
}

.btn_add__input *{
      font-size: 2rem;
      color: var(--text);
}

.namegrad_reset__button{
      margin-top: 4rem;
      gap: 2rem;
      align-items: center;
}

.btn_reset__input{
      padding: 1rem 5rem;
      background: var(--bg-grad-item);
      border-radius: 999px;
      box-shadow: var(--shadow);
      color: var(--text);
      font-size: 1.8rem;
      border: var(--border);
}

.btn_save__preset{
      width: 4rem;
      height: 4rem;
      background: var(--bg-grad-button);
      box-shadow: var(--shadow);
      border-radius: 50%;
      position: relative;
}

.btn_save__preset:after{
      right: -2.4rem;
}

.btn_save__preset *{
      font-size: 2rem;
      color: var(--text);
}

.namegrad_presets{
      margin-top: 6rem;
      gap: 2.5rem;
      flex-wrap: wrap;
}

.namegrad_preset__items{
      width: fit-content;
}

.preset_item__title{
      height: 4rem;
      align-items: center;
      gap: 1rem;
}

.preset_item__title span,
.preset_item__title strong{
      height: 4rem;
      background: var(--bg-item);
      display: flex;
      align-items: center;
      justify-content: center;
      box-shadow: var(--inner-shadow);
}

.preset_item__title span{
      width: 4rem;
      border-radius: 50%;
}

.preset_item__title span *{
      font-size: 2rem;
}

.preset_item__title strong{
      font-size: 1.8rem;
      padding: 0 2.5rem;
      border-radius: 999px;
}

.preset_item__content{
      margin-top: 2.5rem;
}

.item_content__glow{
      align-items: flex-start;
      gap: 2rem;
      justify-content: flex-start;
}

.item_content__rotation{
      width: 100%;
      height: 16rem;
      align-items: center;
      justify-content: center;
      position: relative;
}

.item_content__rotation:before{
      width: 8rem;
      height: 8rem;
      background: var(--bg-grad-button);
      box-shadow: var(--shadow);
      border-radius: 50%;
      border: var(--border);
}

.namegrad_preset_list{
      width: 100%;
      min-width: 25rem;
}

.preset_group{
      gap: 1rem;
}

.namegrad_preset_button{
      margin-top: 4rem;
      justify-content: center;
}

.btn_show__preset{
      padding: 1rem 5rem;
      background: var(--bg-grad-button);
      box-shadow: var(--shadow);
      border-radius: 999px;
      font-size: 1.8rem;
      color: var(--text);
      border: var(--border);
}

.item_content__wave{
      flex-direction: column;
}

.wave_preset{
      margin-top: 2rem;
}

.wave_select{
      flex-direction: column;
      position: relative;
      width: 100%;
      min-width: 25rem;
      max-width: 25rem;
      opacity: 0;
      visibility: hidden;
}

.wave_select.active{
      animation: pulse_wave .5s ease-in-out forwards;
}

@keyframes pulse_wave{
      100%{
            opacity: 1;
            visibility: visible;
      }
}

.wave_select__active{
      padding: 1rem 2rem;
      background: var(--bg-grad-button);
      box-shadow: var(--shadow);
      border-radius: 999px;
      gap: 1rem;
      align-items: center;
      cursor: pointer;
}

.wave_select__active p{
      flex: 1;
      white-space: nowrap;
      text-overflow: ellipsis;
      overflow: hidden;
      font-size: 1.4rem;
}

.wave_select__active span *{
      font-size: 1.6rem;
      color: var(--green);
}

.wave_select__list{
      width: 100%;
      position: absolute;
      top: 10rem;
      left: 0;
      background: var(--bg-grad-button);
      box-shadow: 0 0 0 .1rem rgba(255,255,255,.4), .5rem .5rem 8px rgba(0,0,0,.2);
      border-radius: 1rem;
      gap: .75rem;
      flex-direction: column;
      padding: 2rem;
      opacity: 0;
      visibility: hidden;
}

.wave_select__list.active{
      animation: show_select .3s ease-in-out forwards;
}

@keyframes show_select{
      0%{
            top: 10rem;
            opacity: 1;
            visibility: visible;
      }
      100%{
            top: 7rem;
            opacity: 1;
            visibility: visible;
      }
}

.select_item{
      width: 100%;
      padding: 1rem 0;
      background: var(--bg-item);
      border-radius: .4rem;
      box-shadow: var(--inner-shadow);
      cursor: pointer;
}

.select_item p{
      width: 98%;
      display: block;
      text-align: left;
      padding: 0 1rem;
      white-space: nowrap;
      font-size: 1.3rem;
      text-overflow: ellipsis;
      overflow: hidden;
      transition: .2s;
      color: var(--text);
}

.select_item:hover p{
      color: var(--blue-200);
}

.wrapper_presets,
.wrapper_selected__presets{
      width: 96%;
      left: 50%;
      bottom: 4rem;
      position: fixed;
      transform: translate(-50%, 150%);
      height: 47rem;
      background: var(--bg);
      box-shadow: -1rem -1rem 20px rgba(158, 169, 183, .1), 2rem 5rem 50px rgba(158, 169, 183, .5);
      border-radius: 2rem;
      padding: 1.5rem 2rem 3rem 2rem;
      z-index: 10;
}

.wrapper_presets.show,
.wrapper_selected__presets.show{
      animation: show_all_presets .5s ease-out forwards;
}

@keyframes show_all_presets{
      0%{
            transform: translate(-50%, 150%);
      }
      40%{
            transform: translate(-50%, -20%);
      }
      60%{
            transform: translate(-50%, 20%);
      }
      85%{
            transform: translate(-50%, -10%);
      }
      100%{
            transform: translate(-50%, 0);
      }
}

.presets_headerline,
.selected_headerline{
      height: 4rem;
      align-items: center;
      justify-content: space-between;
}

.presets_headerline__title,
.selected_headerline__title{
      height: 4rem;
      gap: 1rem;
      align-items: center;
}

.presets_headerline__title span,
.selected_headerline__title span{
      width: 4rem;
      height: 4rem;
      background: var(--bg-item);
      box-shadow: var(--inner-shadow);
      border-radius: 50%;
      display: flex;
      align-items: center;
      justify-content: center;
}

.presets_headerline__title span *,
.selected_headerline__title span *{
      font-size: 2.5rem;
}

.presets_headerline__title h2,
.selected_headerline__title h2{
      font-size: 2.4rem;
}

.close_presets__modal{
      width: 4rem;
      height: 4rem;
      background: var(--bg-item);
      box-shadow: var(--inner-shadow);
      border-radius: 50%;
      padding-top: .35rem;
}

.close_presets__modal *{
      font-size: 1.8rem;
      color: var(--text);
}

.presets_main__content,
.selected_presets__content{
      width: 100%;
      height: calc(100% - 8.5rem);
      padding: 2rem;
      border-radius: 1rem;
      margin-top: 2rem;
      overflow-y: auto;
      scrollbar-width: thin;
      scrollbar-color: rgba(0,0,0,.1) var(--text);
}

.presets_main__content::-webkit-scrollbar,
.selected_presets__content::-webkit-scrollbar{
      width: .8rem;
      background: rgba(0,0,0,.1);
      border-radius: 999px;
}

.presets_main__content::-webkit-scrollbar-thumb,
.selected_presets__content::-webkit-scrollbar--thumb{
      background: var(--text);
}

.alert_message{
      position: fixed;
      width: 40rem;
      height: 6rem;
      align-items: center;
      justify-content: center;
      left: calc(50% - 20rem);
      bottom: -10rem;
      z-index: 50;
}

.floating_menu{
      position: fixed;
      right: 4rem;
      bottom: 3rem;
      flex-direction: column;
      align-items: center;
      gap: 2rem;
}

.floating_menu button{
      background: var(--text);
      border-radius: 50%;
      width: 3.6rem;
      height: 3.6rem;
      box-shadow: .5rem .5rem 10px rgba(158, 169, 183,.7);
      position: relative;
}

.floating_menu button:before{
      content: attr(data-tooltip);
      top: 50%;
      left: -1rem;
      transform: translate(-100%, -50%);
      padding: .75rem 1.5rem;
      background: var(--tooltip-bg);
      border-radius: .4rem;
      color: var(--tumblr);
      opacity: 0;
      transition: .2s ease;
}

.floating_menu button:after{
      width: 1.2rem;
      height: 1.2rem;
      background: var(--tooltip-bg);
      top: calc(50% - .6rem);
      left: -1.8rem;
      transform: rotate(45deg);
      opacity: 0;
      transition: .2s ease;
}

.floating_menu button:hover:before,
.floating_menu button:hover:after{
      opacity: 1;
}

.floating_menu button *{
      color: var(--light);
      font-size: 1.8rem;
      padding-top: .3rem;
}

.floating_menu button.btn_open__credits{
      width: 4.4rem;
      height: 4.4rem;
}

.floating_menu button.btn_open__credits *{
      padding-top: 0;
      font-size: 2rem;
}

.floating_info{
      background: #f3f3fb;
      padding: .5rem 2rem;
      align-items: center;
      height: auto;
      min-height: 5rem;
      gap: 2rem;
      border-radius: 999px;
      box-shadow: 1rem 1rem 10px rgba(158, 169, 183, .2);
      position: fixed;
      left: 50%;
      bottom: 5rem;
      transform: translateX(-50%);
      z-index: 10;
}

.floating_info.hidden{
      animation: hide_info .3s ease-in-out forwards;
}

@keyframes hide_info{
      100%{
            bottom: -10rem;
      }
}

.floating_info p{
      font-size: 1.2rem;
      font-weight: 300;
}

.floating_info button{
      width: 3rem;
      height: 3rem;
      border-radius: 50%;
      background: var(--bg-item);
}

.floating_info button *{
      color: var(--text);
}

/** user */

.wrapper_user__modal{
      width: 56rem;
      height: 65rem;
      background: var(--bg-user);
      box-shadow: -1rem -1rem 20px rgba(158, 169, 183, .1), 5rem 5rem 50px rgba(158, 169, 183, .7);
      position: fixed;
      bottom: 6rem;
      right: 10rem;
      transform-origin: right bottom;
      transform: scale(0);
      border-radius: 1rem;
      z-index: 10;
}

.wrapper_user__modal.show{
      animation: show_user .4s ease-in-out forwards;
}

@keyframes show_user{
      0%{
            transform: scale(0);
      }
      70%{
            transform: scale(1.2);
      }
      100%{
            transform: scale(1);
      }
}