.user_content{
      position: relative;
      width: 100%;
      z-index: 10;
}

.btn_close__user{
      width: 3rem;
      height: 3rem;
      border-radius: 50%;
      background: var(--green);
      position: absolute;
      top: 0;
      right: 0;
      transform: translate(50%, -50%);
      z-index: 10;
}

.btn_close__user *{
      color: var(--light);
}

.user_banner{
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 17rem;
      border-radius: 1rem 1rem 0 0;
      overflow: hidden;
}

.user_banner__img{
      width: 100%;
      height: 17rem;
      position: absolute;
      top: 0;
      left: 0;
      align-items: center;
      justify-content: center;
      overflow: hidden;
}

.user_banner__img img{
      width: auto;
      min-width: 100%;
      height: auto;
      min-height: 100%;
}

.user_heading{
      position: relative;
      padding: 2rem;
}

.user_container{
      width: 100%;
      height: auto;
      position: relative;
      padding: 9.5rem 3rem 3rem 3rem;
}

.user_content__info{
      gap: 3rem;
}

.user_data__avatar{
      width: 15rem;
      height: 15rem;
      border: solid .5rem var(--bg-user);
      border-radius: 50%;
      background: var(--bg);
}

.user_data__avatar img{
      width: 14rem;
      height: 14rem;
      border-radius: 50%;
}

.user_data__info{
      margin-top: 1rem;
}

.user_data__info h2{
      font-size: 3rem;
      text-transform: uppercase;
      font-family: var(--ft-3);
      display: flex;
      align-items: baseline;
      gap: .5rem;
      line-height: 3rem;
}

.user_data__info h2 small{
      font-size: 2.2rem;
      top: 80%;
      left: 0;
      font-weight: 600;
      color: var(--green);
}

.user_data__info > span{
      font-size: 2rem;
      font-family: var(--ft-3);
}

.user_location{
      align-items: center;
}

.user_location i *{
      font-size: 2rem;
      color: var(--green);
}

.user_location span{
      font-size: 1.2rem;
      font-family: var(--ft-3);
      white-space: nowrap;
}

.user_data__description{
      flex: 1;
}

.user_data__networks{
      width: 100%;
      height: 4rem;
      align-items: center;
      gap: 1rem;
      position: relative;
      margin-top: 5.5rem;
}

.user_data__networks a{
      color: var(--light);
      background: var(--green);
      padding: .5rem 1.5rem;
      border-radius: 999px;
      font-size: 1.2rem;
}

.user_data__description h3{
      font-size: 3rem;
      color: var(--green);
      margin-top: 1rem;
      position: relative;
}

.user_data__description h3:after{
      width: 4rem;
      height: .4rem;
      background: var(--light);
      left: 0;
      bottom: 0;
}

.user_description__text{
      height: auto;
      max-height: 15rem;
      margin-top: 1rem;
      overflow-y: auto;
      scrollbar-width: thin;
      scrollbar-color: rgba(0,0,0,.05) var(--green)
}

.user_description__text p{
      font-size: 1.5rem;
      font-family: var(--ft-3);
}

.user_data__menu{
      width: 100%;
      height: 4rem;
      align-items: center;
      margin-top: 2rem;
      gap: 1rem;
}

.user_data__menu a{
      width: 4rem;
      height: 4rem;
      background: var(--bg-item);
      display: flex;
      align-items: center;
      justify-content: center;
      box-shadow: var(--inner-shadow);
      border-radius: 50%;
}

.user_data__menu a *{
      font-size: 1.8rem;
      color: var(--text);
}

.user_data__menu a:last-child *{
      color: var(--red-100);
}

.user_skills{
      margin-top: 3rem;
}

.user_skills h2{
      font-size: 2.6rem;
      color: var(--green);
      position: relative;
}

.user_skills h2:after{
      width: 4rem;
      height: .4rem;
      background: var(--light);
      left: 0;
      bottom: 0;
}

.skills_group{
      margin-top: 2rem;
      justify-content: center;
      align-items: center;
      gap: 1.5rem;
}

.skill_item{
      width: 8rem;
      height: 8rem;
      background: var(--bg-item);
      box-shadow: var(--inner-shadow);
      border-radius: .6rem;
      align-items: center;
      justify-content: center;
      flex-direction: column;
}

.skill_item *{
      font-size: 4rem;
}

.skill_item:nth-child(1) *{
      color: var(--html);
}

.skill_item:nth-child(2) *{
      color: var(--css);
}

.skill_item:nth-child(3) *{
      color: var(--js);
}

.skill_item:nth-child(4) *{
      color: var(--php);
}

.skill_item span{
      font-size: 1rem;
      text-transform: uppercase;
}