.presets_group__box{
      flex-wrap: wrap;
      gap: 2rem;
}

.preset_item__box{
      width: 23rem;
      height: 11rem;
      padding: 1rem;
      background: #f3f3fb;
      border-radius: .6rem;
}

.preset_item__m{
      width: 100%;
      height: 6rem;
      border-radius: .4rem;
      cursor: pointer;
      box-shadow: 0 0 5px .3rem rgba(255,255,255,.3) inset;
}

.preset_item__colors{
      width: 100%;
      gap: .5rem;
      margin-top: 1rem;
      justify-content: flex-end;
}

.preset_item__colors span{
      width: 1rem;
      height: 1rem;
      border-radius: 50%;
}