.clip_board__item{
      width: 100%;
      min-width: 23rem;
      height: auto;
      position: relative
}

.clip_board__item textarea{
      width: 100%;
      height: auto;
      min-height: 14rem;
      background: var(--bg-item);
      border: none;
      box-shadow: var(--inner-shadow);
      border-radius: 1rem;
      padding: 1rem;
      font-size: 1.4rem;
      color: var(--text);
      font-style: italic;
}

.clip_board__item textarea:focus{
      outline: none;
}

.btn_copy{
      width: 8rem;
      height: 3rem;
      position: absolute;
      bottom: 1rem;
      right: 1rem;
      background: #cdffd9;
      border: solid .1rem #9bffb6;
      border-radius: .3rem;
      flex-direction: column;
      overflow: hidden;
}

.btn_copy div{
      width: 100%;
      height: 5.8rem;
      flex-direction: column;
      transition: .2s ease-in-out;
}

.btn_copy.copied div{
      margin-top: -2.9rem;
}

.btn_copy div span{
      width: 100%;
      height: 2.9rem;
      line-height: 2.9rem;
      display: flex;
      align-items: center;
      justify-content: center;
      color: #09a22d;
      font-size: 1.2rem;
      gap: .5rem;
      transition: .2s ease-in-out;
}

.btn_copy div span *{
      font-size: 1.4rem;
}
