@import url('https://fonts.googleapis.com/css2?family=Bricolage+Grotesque:opsz,wght@10..48,200;10..48,300;10..48,400;10..48,500;10..48,600;10..48,700;10..48,800&family=Bungee&family=Poppins:wght@200;300;400;500;600;700;800&family=Space+Mono:wght@400;700&display=swap');

:root{
    --ft-1: 'Poppins', sans-serif;
    --ft-2: 'Bricolage Grotesque', sans-serif;
    --ft-3: 'Space Mono', monospace;
    --ft-4: 'Bungee', cursive;

    /** colors */
    --green: #68dfb5;
    --pink: #ee7dd9;
    --blue-100: #42dbe3;
    --blue-200: #5c72e9;
    --light: #fff;
    --red-200: #d11e22;
    --red-100: #e90d0f;
    --alpha: rgba(0,0,0,.1 );
    --grys-300: #a2c2c8;
    --grys-200: #c9d8db;
    --grys-100: #fcffff;

    /** default theme */
    /* kimy*/
    --theme: #8c6bb1;
    --text: #6e778e;
    --glow: #000000;
    --speed: 1s;
    
    --border: solid .1rem rgba(255,255,255,0.48);
    --bg: #e8ebf2;
    --bg-item: #ebeff2;
    --bg-grad-button: linear-gradient(135deg, #e5e9ec, #f3f3fb);
    --bg-grad-item: linear-gradient(135deg, #ebecf1, #e9ecf3);
    --inner-shadow: 5px 5px 6px rgba(158, 169, 183, .5) inset, 1px 1px rgba(255, 255, 255, .8);
    --shadow: -5px -5px 8px rgba(255, 255, 255, .5), 5px 5px 8px rgba(158, 169, 183);
    --shadow-input: .3rem .3rem 10px rgba(158, 169, 183, .4);
    --tooltip-bg: #f9f9f9;
    --bg-user: #e5e9ec;

    /** social color*/
    --facebook: #3b5995;
    --twitter: #1da1f2;
    --google: #dd4b39;
    --youtube: #c4302b;
    --vimeo: #1ab7ea;
    --dribbble: #ea4c89;
    --github: #333;
    --instagram: #c13584;
    --pinterest: #e60023;
    --flickr: #0063dc;
    --bitbucket: #205081;
    --linkedin: #0e76a8;
    --whatsapp: #12af0a;
    --tumblr: #35465c;
    --tiktok: #222;
    --discord: #5865f2;

    /** branch */
    --html: #e44d26;
    --css: #2965f1;
    --js: #ffd600;
    --react: #00d7ff;
    --php: #777bb3;

    /** globals */
    font-family: var(--ft-2);
    font-size: 62.5%;
    color: var(--text);

}

*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
  width: 100%;
  height: 100vh;
  min-height: 100vh;
  overflow-x: hidden;  
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: var(--bg);
}

body *{
  font-size: 1rem;
}

:before,
:after{
  content: '';
  position: absolute;
}

a, span{
  display: inline-block;
}

a{
  text-decoration: none;
}

ul{
  list-style: none;
}

img{
  width: 100%;
  object-fit: cover;
}

button{
  border: none;
  outline: none;
  cursor: pointer;
  font-family: var(--ft-2);
}

.bg_grad{
  display: inline-block;
  color: transparent;
  background: var(--gradient-45);
  -webkit-background-clip: text;
  -moz-background-clip: text;
  background-clip: text;
}

.flex{
  display: flex;
}

.grid{
  display: grid;
}

.col-2{
  grid-template-columns: repeat(2, 1fr);
}

.col-3{
  grid-template-columns: repeat(3, 1fr);
}

.container{
  width: 96%;
  max-width: 120rem;
  margin: 0 auto;
  height: auto;
  position: relative;
}

::-webkit-scrollbar{
  width: .8rem;
  background: rgba(0,0,0,.05);
  border-radius: .8rem;
}

::-webkit-scrollbar-thumb{
  background: var(--green);
  border-radius: .8rem;
}