.preset_item{
      width: 100%;
      height: 4rem;
      padding: .4rem;
      background: var(--bg-grad-item);
      box-shadow: var(--inner-shadow);
      border-radius: .5rem;
      cursor: pointer;
}

.preset_item__bg{
      width: 100%;
      height: 100%;
      border-radius: .5rem;
      box-shadow: 0 0 0 .2rem rgba(255,255,255,.3) inset, 0 0 5px rgba(0,0,0,.2);
}