.wrapper_online{
      height: 4.6rem;
      align-items: center;
      gap: 1rem;
      position: absolute;
      top: 2rem;
      right: 2rem;
}

.online_avatar,
.online_avatar img{
      width: 4rem;
      height: 4rem;
      border-radius: 50%;
}

.online_avatar{
      position: relative;
      background: rgba(0,0,0,.05);
}

.online_dots{
      width: 1.4rem;
      height: 1.4rem;
      position: absolute;
      top: 0;
      right: 0;
      border: solid .2rem rgba(0,0,0,.3);
      border-radius: 50%;
}

.online_dots.green{
      background: var(--green);
}

.online_dots.red{
      background: var(--red-100);
}

.online_status{
      font-size: 1.8rem;
      position: relative;
      color: var(--light);
}

.online_status:before{
      width: 4rem;
      height: .4rem;
      border-radius: 999px;
      left: 0;
      bottom: -.4rem;
}

.online_status.red:before{
      background: var(--red-100);
}

.online_status.green:before{
      background: var(--green);
}