.selected_presets__box{
      flex-wrap: wrap;
      gap: 2rem;
}

.selected_item{
      width: 25rem;
      height: 17rem;
      background: #f3f3fb;
      border-radius: 1rem;
      padding: 1.5rem 1rem;
}

.selected_item__headerline{
      position: relative;
      justify-content: center;
      width: 100%;
      height: 3rem;
      align-items: center;
}

.selected_item__text h2{
      font-size: 2rem;
      color: transparent;
      -webkit-background-clip: text;
      -moz-background-clip: text;
      background-clip: text;
}

.btn_delete__item{
      width: 2.8rem;
      height: 2.8rem;
      background: var(--bg-item);
      box-shadow: var(--inner-shadow);
      position: absolute;
      top: 0;
      right: 0;
      border-radius: 50%;
}

.btn_delete__item *{
      color: var(--text);
      padding-top: .2rem;
      font-size: 1.4rem;
}

.selected_item__clipboard{
      margin-top: 1rem;
}

.clip_board__item.selected textarea{
      min-height: 10rem;
}